import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    typelist: [],
    product: {},
    role: {
      role: "",
      permission: []
    }
  },
  mutations: {
    setTypeList(state, list) {
      state.typelist = list;
    },
    setProduct(state, item) {
      state.product = item;
    },
    setRole(state, item) {
      state.role.permission = item.permission.map(Number);
      state.role.role = item.userinfo.role;
    }
  },
  actions: {
    getTypeList(context) {
      Vue.prototype
        .$axios({
          url: "/back/product/getAllType"
        })
        .then(res => {
          if (res.data.code == 200) {
            context.commit("setTypeList", res.data.data);
          }
        });
    }
  },
  modules: {}
});
