import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const originalPush = VueRouter.prototype.push;

VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};

const routes = [
  {
    path: "/",
    name: "login",
    component: () => import("../views/login.vue")
  },
  {
    path: "/home",
    name: "home",
    component: () => import("../views/home.vue"),
    children: [
      {
        path: "/home/commodity",
        name: "commodity",
        component: () => import("../views/home/commodity.vue")
      },
      {
        path: "/home/checkorder",
        name: "checkorder",
        component: () => import("../views/home/checkOrder.vue")
      },
      {
        path: "/home/editcommodity",
        name: "editcommodity",
        component: () => import("../views/home/editCommodity.vue")
      },
      {
        path: "/home/goodshistory",
        name: "goodshistory",
        component: () => import("../views/home/goodsHistory.vue")
      },
      {
        path: "/home/order",
        name: "order",
        component: () => import("../views/home/order.vue")
      },
      {
        path: "/home/freight",
        name: "freight",
        component: () => import("../views/home/freight.vue")
      },
      {
        path: "/home/goods",
        name: "goods",
        component: () => import("../views/home/goods.vue")
      },
      {
        path: "/home/memberinfo",
        name: "memberinfo",
        component: () => import("../views/home/memberInfo.vue")
      },
      {
        path: "/home/memberlist",
        name: "memberlist",
        component: () => import("../views/home/memberList.vue")
      },
      {
        path: "/home/memberorder",
        name: "memberorder",
        component: () => import("../views/home/memberOrder.vue")
      },
      {
        path: "/home/mechanism",
        name: "mechanism",
        component: () => import("../views/home/mechanism.vue")
      },
      {
        path: "/home/permission",
        name: "permission",
        component: () => import("../views/home/permission.vue")
      },
      {
        path: "/home/analysis",
        name: "analysis",
        component: () => import("../views/home/dataAnalysis.vue")
      }
    ]
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});
router.beforeEach((to, from, next) => {
  const routeMap = [
    "commodity",
    "order",
    "freight",
    "goods",
    "memberinfo",
    "mechanism",
    "analysis"
  ];
  if (to.path === "/") return next();
  let role;
  if (router.app.$options.store.state.role.role)
    role = router.app.$options.store.state.role;
  else {
    role = JSON.parse(sessionStorage.getItem("store"))
      ? JSON.parse(sessionStorage.getItem("store")).role
      : "";
  }
  if (!role.role) return next("/");
  //没有获取到权限就跳转登录页面
  else if (routeMap.indexOf(to.name) !== -1) {
    if (role.permission.includes(routeMap.indexOf(to.name))) next();
    else next({ path: "/" });
  } else if (to.name === "editcommodity") {
    //如果进订单详情页面则判断是否有订单页面的权限
    if (role.permission.includes(routeMap.indexOf("commodity"))) next();
    else next({ path: "/" });
  }
  next();
});

export default router;
