<template>
  <div id="app">
    <el-header class="home-header">
      <el-row :gutter="20">
        <el-col :span="12" class="home-header-left">
          <div class="home-header-logowrapper">
            <img src="/logo.jpg" class="home-header-logo" />
            <span>
              <router-link to="/home">
                一木兴后台管理系统
              </router-link>
            </span>
          </div>
        </el-col>
        <el-col :span="12" class="home-header-user">
          <span class="logout" @click="logout">
            退出登录
          </span>
        </el-col>
      </el-row>
    </el-header>
    <router-view />
    <el-footer class="home-footer">
      <footer>技术支持：成都花行科技有限公司</footer>
    </el-footer>
  </div>
</template>

<script>
export default {
  methods: {
    logout() {
      this.$router.push({ name: "login" });
    }
  },
  created() {
    if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
    }
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    });
  }
};
</script>

<style lang="less">
@bgcolor: rgba(0, 25, 54, 0.884);
.home-header {
  background-color: @bgcolor;

  .home-header-left {
    height: 60px;

    .home-header-logowrapper {
      display: flex;
      align-items: center;

      .home-header-logo {
        width: 40px;
        height: 40px;
      }

      span {
        text-align: center;
        line-height: 60px;
        margin-left: 10px;

        a {
          color: #ffffff;
          text-decoration: none;
          font-size: 1.4rem;
        }
      }
    }
  }

  .home-header-user {
    text-align: right;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    span {
      line-height: 60px;
    }

    .logout {
      color: #ffffff;
      text-decoration: underline;
      &:hover {
        cursor: pointer;
      }
    }
  }
}

.home-footer {
  background-color: @bgcolor;
  color: #fff;

  footer {
    line-height: 60px;
  }
}

#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
html,
body,
#app {
  margin: 0;
  height: 100%;
}

#nav {
  padding: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
</style>
